/* Media Queries */

@media only screen and (max-width: 1600px) {
  .black-circle {
    height: 500px;
    width: 500px;
    background: #121212;
    border-radius: 50%;
    position: absolute;
    z-index: 0;
    margin-top: -200px;
    margin-left: 200px;
  }
}

@media only screen and (max-width: 1300px) {
  .navbar-item {
    font-size: 12px!important;
  }

  .g-button {
    font-size: 12px!important;
    width: 160px!important;
  }
  
  .demo-button {
    font-size: 12px!important;
  }

  .black-circle {
    height: 500px;
    width: 500px;
    background: #121212;
    border-radius: 50%;
    position: absolute;
    z-index: 0;
    margin-top: -200px;
    margin-left: 150px;
  }
}

@media only screen and (max-width: 1150px) {
  .navbar-menu {
    margin-left: 100px!important;
  }

  .hero-content div h1 {
    font-size: 20px!important;
  }

  .product-info h1 {
    font-size: 36px!important;
  }

  .hero-img {
    max-width: 75%;
    padding-top: 45px;
    margin: auto;
  }

  .product-rect {
    position: absolute;
    width: 100vw;
    height: 75px;
    z-index: -1;
    background: #FFBD27;
    margin-top: 250px;
  }

  .black-circle {
    height: 500px;
    width: 500px;
    background: #121212;
    border-radius: 50%;
    position: absolute;
    z-index: 0;
    margin-top: -200px;
    margin-left: 100px;
  }
}

@media only screen and (max-width: 1000px) {
  .orange-circle {
    height: 500px;
    width: 500px;
    background: #FFBD27;
    border-radius: 50%;
    position: absolute;
    z-index: 0;
    margin-top: -100px;
  }

  .orange-column {
    margin-top: 200px;
    margin-left: 100px;
  }
}
@media only screen and (max-width: 760px) {
  .icon-div img {
    max-width: 15%;
    margin: auto;
    margin-top: 60px;
  }
  .hero-img {
    max-width: 50%;
    padding-top: 45px;
    margin: auto;
  }

  .product-nav {
    display: none;
  }

  .product-rect {
    position: absolute;
    width: 100vw;
    height: 60px;
    z-index: -1;
    background: #FFBD27;
    margin-top: 200px;
  }

  .product-features {
    margin-top: 60px;
  }

  div.product-features div div h3 {
    font-size: 26px!important;
  }

}

@media only screen and (max-width: 600px) {
  .logo {
    margin-left: 10px;
  }

  .product-rect {
    position: absolute;
    width: 100vw;
    height: 50px;
    z-index: -1;
    background: #FFBD27;
    margin-top: 150px;
  }

  .custom-modal div {
    padding: 0!important;
    text-align: center;
    margin-top: 20px!important;
  }

  .details-one h1, .enterprise-summary h1 {
    font-size: 36px!important;
  }
}

@media only screen and (max-width: 1020px) {
  .logo {
    margin-left: 10px;
  }
  .main-nav {
    background: #202021!important;
    height: 50px!important;
  }

  .navbar-item {
    color: #202021!important;
  }
  #navbarBasicExample {
    width: 300px!important;
    float: right!important;
  }

  .fade-in {
    opacity: 1;
    -webkit-animation-name: fadeInOpacity;
            animation-name: fadeInOpacity;
    -webkit-animation-iteration-count: 1;
            animation-iteration-count: 1;
    -webkit-animation-timing-function: ease-in;
            animation-timing-function: ease-in;
    -webkit-animation-duration: 0.5s;
            animation-duration: 0.5s;
  }

  .fade-out {
    opacity: 0;
    -webkit-animation-name: fadeOutOpacity;
            animation-name: fadeOutOpacity;
    -webkit-animation-iteration-count: 1;
            animation-iteration-count: 1;
    -webkit-animation-timing-function: ease-in;
            animation-timing-function: ease-in;
    -webkit-animation-duration: 0.5s;
            animation-duration: 0.5s;
  }
  
  @-webkit-keyframes fadeInOpacity {
    0% {
      opacity: 0;
    }
    100% {
      opacity: 1;
    }
  }
  
  @keyframes fadeInOpacity {
    0% {
      opacity: 0;
    }
    100% {
      opacity: 1;
    }
  }

  @-webkit-keyframes fadeOutOpacity {
    0% {
      opacity: 1;
    }
    100% {
      opacity: 0;
    }
  }

  @keyframes fadeOutOpacity {
    0% {
      opacity: 1;
    }
    100% {
      opacity: 0;
    }
  }
}
